






























import { Vue, Component, Prop, Watch } from 'vue-property-decorator';
import { personEmpty } from '@/../helpers';

// vue-easy-lightbox
import Lightbox from 'vue-easy-lightbox';
Vue.use(Lightbox);

interface ILightboxItem {
    imgs: string;
    visible: boolean;
    index: number;
}

@Component({
    components: {},
})
export default class ImageView extends Vue {
    //#region prop
    @Prop({
        type: String,
        default: '',
    })
    private id: string;

    @Prop({
        type: String,
        default: personEmpty,
    })
    private src: string;

    @Prop({
        type: String,
        default: personEmpty,
    })
    private orignalSrc: string;

    @Prop({
        type: Number,
        default: 150,
    })
    private width: number;

    @Prop({
        type: Number,
        default: 150,
    })
    private height: number;
    //#endregion prop

    //#region variable
    private faceImgSrc: string = personEmpty;
    private isShowOriginalImage: boolean = false;

    private lightboxItem: ILightboxItem = {
        imgs: '', // Img Url , string or Array
        visible: false,
        index: 0, // default: 0
    };
    //#endregion variable

    //#region vue life

    private mounted(): void {
        this.initSrc();
    }
    //#endregion vue life

    //#region watch
    @Watch('src', { immediate: true, deep: true })
    private formSrcChanged(newVal: string, oldVal: string): void {
        this.initSrc();
    }

    @Watch('orignalSrc', { immediate: true, deep: true })
    private formOrignalSrcChanged(newVal: string, oldVal: string): void {}
    //#endregion watch

    //#region init Data
    private initSrc(): void {
        this.faceImgSrc = this.src;
    }
    //#endregion init Data

    //#regionclick
    private clickViewOriginal(): void {
        this.isShowOriginalImage = true;
        this.lightboxItem = {
            imgs: this.orignalSrc, // Img Url , string or Array
            visible: true,
            index: 0, // default: 0
        };
    }

    private clickHide(): void {
        this.lightboxItem.visible = false;
        this.isShowOriginalImage = false;
    }
    //#endregion click

    //#region handle error
    private handleImageError(): void {
        this.faceImgSrc = personEmpty;
    }
    //#endregion handle error
}
